import orderType from '../../common/public.js'
export default {
	data() {
		return {
			imgUrl: 'http://106.15.194.58/images/', //图片接口
			curHdIndex: '0',
			orderId: '',
			orderList: [],
			type: 2,
			// orderList:[]
		}
	},
	onShow() {
		orderType.delOrderType = true
	},
	onLoad(e) {
		this.getOrderList(1);
	},
	methods: {
		tabFun(index) { //切换tab
			this.curHdIndex = index - 1;
			this.getOrderList(index);
			//请求数据，替换orderList
			//	this.orderList = [];
		},
		waitingMeal(type,orderId) { //跳待取餐
			uni.navigateTo({
				url: '../waitingMeal/waitingMeal?id='+orderId+'&type='+this.type,
				animationType: 'slide-in-right',
				animationDuration: 200
			})
			
		},
		/**
		 * 获取订单列表
		 * type: number -1 取消的 0-待提交的 1-待接收 2-待取餐 3-待评价 4-已评价
		 */
		getOrderList(index) {
			let type = ''
			switch (index){
				case 1:
				type = 2
				break
				case 2:
				type = 3
				break
				case 3:
				type = 4
				break
				case 4:
				type = 0
				break
			}
			uni.showLoading({
				mask: true
			})
			// console.log(type)
			this.type = type
			uni.request({
				url: this.nowUrl + '/foods/myOrder/query',
				method: 'get',
				data: {
					type: type
				},
				header: {
					"content-type": "application/json",
					"token": this.token
				},
				success: (res) => {
					// console.log(res.data);
					this.orderList = res.data.data;
					uni.hideLoading()
				}
			});
		}
	}
}
